import { apiBaseUrl } from "../apiBaseUrl";
import {
    REQUEST_ADD_EVENT,
    REQUEST_ADD_EVENT_SUCCESS,
    REQUEST_ADD_EVENT_FAILURE,
    REQUEST_GET_EVENT_LIST,
    REQUEST_GET_EVENT_LIST_SUCCESS,
    REQUEST_GET_EVENT_LIST_FAILURE,
    REQUEST_DELETE_EVENT,
    REQUEST_DELETE_EVENT_SUCCESS,
    REQUEST_DELETE_EVENT_FAILURE,
    REQUEST_DETAIL_EVENT,
    REQUEST_DETAIL_EVENT_SUCCESS,
    REQUEST_UPDATE_EVENT,
    REQUEST_UPDATE_EVENT_SUCCESS,
    REQUEST_UPDATE_EVENT_FAILURE,
    REQUEST_REGISTRATION_EVENT,
    REQUEST_REGISTRATION_EVENT_SUCCESS,
    REQUEST_REGISTRATION_EVENT_FAILURE,
    REQUEST_CHECK_REGISTRATION_EVENT,
    REQUEST_CHECK_REGISTRATION_EVENT_SUCCESS,
    REQUEST_CHECK_REGISTRATION_EVENT_FAILED,
    REQUEST_REGISTRATION_EVENT_LIST,
    REQUEST_REGISTRATION_EVENT_LIST_SUCCESS,
    REQUEST_REGISTRATION_EVENT_LIST_FAILED
} from "../constant/EventConstant";
import { getError } from "../helper/handlingError";

export const fetchAddEventAction = (requestParam, statusEvent) => {
    return async (dispatch) => {
        dispatch({
            type: REQUEST_ADD_EVENT,
        });
        try {
            const token = localStorage.getItem('token');
            const { data, status } = await apiBaseUrl({
                method: 'POST',
                params: {},
                headers: {
                    'Content-type': 'application/json',
                    Authorization: `Bearer ${token}`,
                },
                url: `/api/v2/event/add/${statusEvent}`,
                data: requestParam
            });
            if (status === 200) {
                dispatch({
                    type: REQUEST_ADD_EVENT_SUCCESS,
                    payload: data,
                });
                return data;
            }
        } catch (error) {
            dispatch({
                type: REQUEST_ADD_EVENT_FAILURE,
            });
            await getError(error);
        }
    };
}

