import { useDispatch, useSelector } from "react-redux";
import { useLocation } from "react-router-dom";
import { useEffect, useState } from "react";
import { successAlert, warningAlert } from "../../../app/service/helper/handlingError";
import Loading from "../../../components/Loading/Loading";
import { Container, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@mui/material";
import TitleComponent from "../../../components/Title/TitleComponent";
import {
    fetchCheckRegistrationEventAction, fetchDeleteRegistrationEventListByMemberIdAction,
    fetchRegistrationEventListByMemberIdAction
} from "../../../app/service/action/eventAction";
import { confirmAlert } from 'react-confirm-alert';
import { fetchAllMemberByUserIdAction } from "../../../app/service/action/usersAction";
import { indonesianDate } from "../../../utils/DateUtils";
import moment from "moment";
import { CancelOutlined } from "@mui/icons-material";
import Button from "@mui/material/Button";
import SelectBox from "../../../components/Input/SelectBox";
import { statusRegistrationEvent } from "../../../utils/Utils";

const INITIAL_OBJECT = {
    eventId: 0,
    registrationMemberId: 0
}
export const RegistrationEvent = ({ id }) => {
    const dispatch = useDispatch();
    const location = useLocation();
    const receivedData = location.state;
    const [dataOptionRider, setDataOptionRider] = useState([{ value: '', name: '--Pilih Rider--' }]);
    const [requestParam, setRequestParam] = useState(INITIAL_OBJECT);
    const [dataRider, setDataRider] = useState([]);
    const [isClosed, setIsClosed] = useState(false)

    const updateFormValue = ({ updateType, value }) => {
        // setErrorMessage("")
        setRequestParam({ ...requestParam, [updateType]: value, eventId: id })
    }
    const data = useSelector(({ membersByUserIdState }) => (membersByUserIdState.membersByUserId.data))
    const isLoading = useSelector(({ membersByUserIdState }) => (membersByUserIdState.membersByUserId.isLoading))
    const status = useSelector(({ membersByUserIdState }) => (membersByUserIdState.membersByUserId.status))
    useEffect(() => {
        console.log(data.result)
        if (status === "ready" && data.result) {
            const update = data.result.map((item) => ({
                value: item.id,
                name: item.name_rider
            }))
            setDataOptionRider([{ value: '', name: '--Pilih Rider--' }, ...update])

            dispatch(fetchRegistrationEventListByMemberIdAction({
                eventId: parseInt(id),
                registrationMemberId: update.map((item) => item.value)
            })).then((res) => {
                console.log('cek res', res)
                setDataRider(res.result)
            });
        }
    }, [data.result, dispatch, id, status]);

    useEffect(() => {
        dispatch(fetchAllMemberByUserIdAction())
    }, [dispatch])

    useEffect(() => {
        if (requestParam.registrationMemberId) {
            dispatch(fetchCheckRegistrationEventAction(requestParam)).then((res) => {
                if (res.result) {
                    if (res.responseData.code === 200) {
                        warningAlert(res.responseData.message)
                    }
                    if (res.responseData.code === 201) {
                        successAlert(res.responseData.message)
                        setTimeout(() => {
                            dispatch(fetchAllMemberByUserIdAction())
                        }, 2000)
                    }
                }
            })
        }
    }, [dispatch, requestParam]);

    const handleCancelRegistration = (id) => {
        confirmAlert({
            title: 'Konfirmasi Pembatalan Registrasi',
            message: 'Apakah anda yakin akan membatalakan registrasi?',
            buttons: [
                {
                    label: 'Ya, Yakin',
                    onClick: () => {
                        dispatch(fetchDeleteRegistrationEventListByMemberIdAction(id))
                            .then((res) => {
                                if (res.result) {
                                    successAlert("Pembatalan registasi event berhasil!!!")
                                    setTimeout(() => {
                                        dispatch(fetchAllMemberByUserIdAction())
                                    }, 2000)
                                }
                            })
                    }
                },
                {
                    label: 'Tidak',
                }
            ]
        });
    }
    useEffect(() => {
        const date = new Date();
        const closeDate = moment(receivedData.closed, 'YYYY-MM-DD HH:mm:ss').toDate();
        if (closeDate < date) {
            setIsClosed(true)
        }
    }, [receivedData.closed])

    return (
        isLoading ? <Loading /> :
            <Container>
                <div id='registration-event'>
                    <TitleComponent title={`Registration Event ${receivedData.name}`} />
                    <span>{indonesianDate(receivedData.date)}</span>
                    {isClosed ?
                        <div className='notify-closed'>
                            <span>Registrasi event telah ditutup!!!</span>
                        </div> :
                        <SelectBox label={'Pilih Rider'} defaultValue={""} disabled={isClosed}
                            updateFormValue={updateFormValue} updateType={'registrationMemberId'}
                            options={dataOptionRider} />
                    }
                </div>
                <div>
                    <TitleComponent title={'Data Rider yang terdaftar'} />
                    <TableContainer component={Paper}>
                        <Table sx={{ minWidth: 250 }} aria-label="simple table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Nama Rider</TableCell>
                                    <TableCell>Action</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {dataRider ? dataRider.map((item) => (
                                    <TableRow>
                                        <TableCell>
                                           <strong>{item.rider_name}</strong><br />
                                            {item.categoryName}<br/>
                                            <Button size="small" variant="contained"
                                                    color={parseInt(item.status) ? 'primary' : 'error'}>
                                                {statusRegistrationEvent(parseInt(item.status))}
                                            </Button>
                                        </TableCell>
                                        <TableCell>
                                            <div className="icon-cancel"
                                                onClick={() => handleCancelRegistration(item.id)}>
                                                <CancelOutlined /> <span>Cancel</span>
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                )) : (<TableRow>
                                    <TableCell>Tidak ada rider yang terdaftar</TableCell>
                                </TableRow>)}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>
            </Container>
    )
}
